
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  background-color: #111827;
  font-family: 'Poppins', sans-serif;
}

.appbar {
  border-bottom: .1px solid #678;
}

.card {
  max-width: 270px;
  min-width: 240px;
  margin: 1em;
  position: relative;
  float: left;
  border: .1px solid #678;

  &:hover {
    box-shadow: #3466F6 0px 5px;
  }
}

.delete-btn {
  background-color: transparent;
  position: relative;
  float: right;
  border: none;
  width: 36px;
  height: 36px;
  cursor: pointer;
  outline: none;
}

@media only screen and (max-width: 600px) {

  .appbar {
    padding-top: .5rem;
    padding-bottom: .5rem;
  }

  .card {
    max-width: 480px;
    &:hover {
      box-shadow: none;
    }
  }
}

